.todoContainer{
    width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 150px auto;
    padding-top: 1rem;
}

.titulo{
display: flex;
justify-content: center;
}

.todoCreateForm{
    display: flex;
    gap: 5px
}

.todoInput{
    flex-grow: 1;
    width: 100%;
    padding: 10px;
    border-radius: 3px;
    border: solid 3px black
}

.buttonCreate{
    border: none;
    border-radius: 3px;
    background-color: rgb(26, 76, 169);
    color: rgb(248, 252, 255);
    padding: 15px 30px;
}

.todoContainer{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 1rem;
}

.todo{
    padding: 10px;
    background-color: rgb(255, 248, 240);
    border-bottom: solid 1px ;
}

.todoTitle{
    flex: 1;
    font-size: 25px;
}

.todoUpdateForm, .todoInfo{
    display: flex;
    align-items: center;
    gap: 5px;
}

.button{
    border: none;
    border-radius: 3px;
    background-color: rgb(72, 122, 221);
    color: white;
    padding: 15px 23px;
}

.buttonDelete{
    border: none;
    border-radius: 3px;
    background-color: rgb(169, 26, 26);
    color: white;
    padding: 15px 23px;
}